/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from 'react';
import { Table, Pagination, Input, } from 'antd';
// icons
import axios from 'axios';
import { Select, FormControl, MenuItem, InputLabel, Box, Typography } from '@mui/material'
import { styled, alpha } from '@mui/material/styles';
import Page from '../../../components/Page';
import './style.css'

export default function EcommerceProductList() {
	const [data, setData] = useState([]);
	const [perpage, setPerpage] = useState(10);
	const [current, setCurrent] = useState(1);
	const [total, setTotal] = useState(0);
	const [search, Setsearch] = useState('');
	const [search2, Setsearch2] = useState('');
	const [order, setOrder] = useState('descend');
	const [field, setField] = useState('id');

	const PageTitle = styled('div')(({ theme }) => ({
		backgroundColor: `${alpha(theme.palette.primary.main, 0.08)}`
	}));

	const toDate = (value) => {
		const currentDate = new Date(value);
		const year = currentDate.getFullYear();
		const month = String(currentDate.getMonth() + 1).padStart(2, '0');
		const day = String(currentDate.getDate()).padStart(2, '0');
		return `${year}-${month}-${day}`;
	}

	const toTime = (currentTime) => {
		const dateObj = new Date(currentTime);
		const hours = String(dateObj.getHours() + 1).padStart(2, '0');
		const minutes = String(dateObj.getMinutes() + 1).padStart(2, '0');
		const seconds = String(dateObj.getSeconds() + 1).padStart(2, '0');
		return `${hours}:${minutes}:${seconds}`;
	}

	const columns = [
		{
			title: 'Sender',
			dataIndex: 'fromName',
		},
		{
			title: 'Email',
			dataIndex: 'fromEmail',
		},
		{
			title: 'Date (Receive)',
			render: (_, id) => (
				<span>{toDate(id.createdAt)}</span>
			),
		},
		{
			title: 'Time (Receive)',
			render: (_, id) => (
				<span>{toTime(id.createdAt)}</span>
			),
		},
		{
			title: 'Handled (Agent Name)',
			dataIndex: 'accept',
		},
		{
			title: 'Date (Accept)',
			render: (_, id) => (
				<span>
					{id.acceptDate && toDate(id.acceptDate)}
				</span>
			),
		},
		{
			title: 'Time (Accept)',
			render: (_, id) => (
				<span>
					{id.acceptDate && toTime(id.acceptDate)}
				</span>
			),
		},
		{
			title: 'Date (Reply)',
			render: (_, id) => (
				<span>
					{id.reply && toDate(id.reply)}
				</span>
			),
		},
		{
			title: 'Date (Reply)',
			render: (_, id) => (
				<span>
					{id.reply && toTime(id.reply)}
				</span>
			),
		},
	];

	const onChange = (pagination, filters, sorter, extra) => {
		if (sorter.order) {
			setOrder(sorter.order);
			if (sorter.field === 'key') {
				setField('id');
			}
			else {
				setField(sorter.field);
			}

		}
		else {
			setOrder('descend');
			setField('id');
		}
	};

	const onPageChange = (page, pageSize) => {
		console.log(page, pageSize)
		setCurrent(page);
		setPerpage(pageSize);
	}

	const handlekeypress = (event) => {
		if (event.key === "Enter") {
			Setsearch2(search);
		}
	}

	useEffect(() => {
		let data = {
			page: current,
			perpage: perpage,
			search: search2,
		}
		axios
			.post(`${process.env.REACT_APP_SERVER_URL}/getservice`, data)
			.then((res) => {
				console.log(res.data)
				setData(res.data.data);
				setTotal(res.data.total);
			})
	}, [current, perpage, search2, order, field]);

	return (
		<>
			<Page title="Contacts">
				<PageTitle>
					<Typography
						color="text.primary"
						sx={{
							textTransform: 'uppercase',
							fontSize: '23px',
							fontWeight: 'bold',
							padding: '10px',
							mb: '20px'
						}}>
						SERVICE
					</Typography>
				</PageTitle>
				<Box>
					<Box className='toolbar'>
						<Box sx={{ flex: '1 1 0%', textAlign: { xs: 'let', lg: 'right' } }}>
							<FormControl sx={{ width: '100px', mb: '10px', mr: '10px' }} size="small">
								<InputLabel id="select-subcategory"></InputLabel>
								<Select
									labelId="select-subcategory"
									id="subcategory"
									value={perpage}
									onChange={(e) => setPerpage(e.target.value)}
								>
									<MenuItem value={5}>5</MenuItem>
									<MenuItem value={10}>10</MenuItem>
									<MenuItem value={25}>25</MenuItem>
									<MenuItem value={50}>50</MenuItem>
								</Select>
							</FormControl>
							<Input onChange={(e) => Setsearch(e.target.value)} onKeyPress={handlekeypress} size="large" placeholder="Search..." style={{ width: '200px', marginBottom: '10px', marginRight: '20px' }} />
						</Box>
					</Box>
				</Box>
				<Box>
					<Table className='table-scroll' bordered='true' columns={columns} dataSource={data} onChange={onChange} pagination={false} />
				</Box>
				<Pagination
					onChange={onPageChange}
					total={total}
					// itemRender={itemRender}
					pageSize={perpage}
					style={{ marginTop: '20px', textAlign: 'right' }} />
			</Page>
		</>
	);
}

